<template>
    <div class="graybg">
        
        <!-- <div > -->
        <div v-if="$store.state.updProd.pictures"
            :class="['pl-15 smooth', ($store.state.updProd.pictures.length > 0) && 'picsArrBg pt-15 pb-8 smooth-show' ]" >
        <!-- <div :class="['picsArrBg pt-15 pb-15 smooth', ($store.state.updProd.pictures.length > 0) && 'smooth-show']" > -->
            <!-- <div> -->
            <ul >
                <li v-for="item in $store.state.updProd.pictures"
                    :key="item"
                    :class="['flex-list', item.includes('_COPY') && 'orange']"
                    >
                    <a :href=" define.pathToUploads + '/' + item " className='' target='_blank' rel='noreferrer noopener' title=''
                        ><img :src=" define.prevFolder2 + item " class="prev" alt=""></a>
                    <!-- <div>
                        {{ item.length > define.maxNameLength ? item.substr(0, define.maxNameLength - 13) + ' ... ' + item.substr(-8, 8) : item }}
                    </div> -->
                    <div class='picName'>
                        <a :href=" define.pathToUploads + '/' + item " className='' target='_blank' rel='noreferrer noopener' title='Открыть в новой вкладке'
                        >{{ (item.length > maxNameLength) ? item.substr(0, maxNameLength - 13) + ' ... ' + item.substr(-8, 8) : item }}
                        </a>
                    </div>

                    <!-- <button @click="preparePicToDelete(item)">удалить</button> -->
                    <div class='delPic'
                        @click="preparePicToDelete(item)"><img :src=" define.pathToGraphics + '/delete2_20px.png' " alt=''/></div>
                </li>
            </ul>
            <!-- </div> -->
        </div>
        <!-- </div> -->
        
        <div class="multiSelect mb-15">
            <div class="flex mr-15">
                <input type="file" id="files" ref="files" accept=".png, .jpg, .jpeg, .gif"   multiple @change="handleFilesUpload()"/>
                <br>
                <button
                    :disabled="uplBtnBlock"
                    @click="submitFiles()"
                >{{ uploadButton }}</button>
            </div>
            
            <div :class="[ 'smooth', isInProgress && 'smooth-show', '']">
                <div class="mt-20 mb-10">
                    {{ uploadPercentage }} % <br>
                    <div class="progress">
                    <div class="progress-bar" :style="{ width: + uploadPercentage + '%' }"></div>
                    </div>
                </div>
            </div>
            <transition name="fade">
                <div v-show="newPics.length > 0 ? true : false" class="mt-10 mb-10">{{ uploadedFiles }} ({{ newPics.length }}):</div>
            </transition>
            <ul class="pl-15">
            <transition-group name="list">
                <li v-for="item in newPics"
                    :key="item"
                    :class="['flex-list', item.includes('_COPY') && 'orange']">
                    <img :src=" define.prevFolder + item" class="prev" alt="">
                    <div className='picName'>
                        {{ item.length > maxNameLength ? item.substr(0, maxNameLength - 13) + ' ... ' + item.substr(-8, 8) : item }} 
                    </div>
                    <div className='delPic'
                        @click="deleteNewPics(item)"
                        ><img :src=" define.pathToGraphics + '/delete2_20px.png' " alt=''/></div> 
                </li>
            </transition-group>
            </ul>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { define, maxNameLength } from '@/data/globals.js'
import useNotify from '@/hooks/useNotify'
import notifys from '@/data/languages/notifications/MultiUploadData'
import useLang from '@/hooks/useLang'

export default {
props: {
    uploadButton: {
        type: String,
        default: 'Загрузить',
    },
    uploadedFiles: {
        type: String,
        default: 'Загружены файлы',
    },
},
data(){
    return {
        files: '',
        newPics: [],
        picsToDelete: [],
        isInProgress: false,
        uploadPercentage: 0,
        uplBtnBlock: true,
        // pathToFiles: 'uploads/',
        // maxLength: 40, // показываем сокращенное имя файла, если оно большеm maxLength
        // prevFolder: define.pathToUploads + '/40px/',
        // pathToGraphics: define.pathToGraphics,
    }
},
methods: {
    // Закрыто или открыто окно выбора файлов
    handleFilesUpload(){
        this.files = this.$refs.files.files;
        this.isInProgress = true
        console.log('this.$refs.files.files.length: ', this.$refs.files.files.length);
        // Если выбраны файлы, то кнопку "загрузить" можно разблокировать
        if(this.$refs.files.files.length > 0) this.uplBtnBlock = false
        else this.uplBtnBlock = true
    },
    // Загрузка файлов
    async submitFiles(){
        this.uploadPercentage = 0
        this.uplBtnBlock = true

        let file
        let formData = new FormData();

        for( let i = 0; i < this.files.length; i++){
            file = this.files[i];
            // console.log('file: ', file);
            formData.append('files[' + i + ']', file);
        }// for

        try {
            const response = await axios({
                method: 'post',
                url: define.pathToAPI + '/multiupload/multiupload.php',
                data: formData,
                withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true")
                // headers: {
                //     // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
                //     Accept: 'application/json',
                //     'Content-Type': 'application/json',
                // },
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: function(progressEvent) {
                    this.uploadPercentage = Math.round( (progressEvent.loaded * 100) / progressEvent.total )
                }.bind(this)
            })
            console.log('response: ', response);
            
            if(!response.data.uplFiles) {
                // this.uploadPercentage = 0
                this.pushNotify( notifys[this.lang].fileSaveError, 'notifyOrange')
                console.log('! Не получен response.data.uplFiles');
                // alert('Ошибка на сервере при сохранении файлов (см. консоль)')
                return
            }

            // проверка ответа от php файла
            if(response.data) {
                // // Включение заморозки действий
                // this.$store.commit('SetFreeze', true)
                // Если в newPics еще нет такого элемента, тогда добавляем
                for( let j = 0; j < response.data.uplFiles.length; j++) {
                    if (!this.newPics.includes(response.data.uplFiles[j])) {
                        this.newPics.push(response.data.uplFiles[j]);
                    }
                }// for
            } else {
                console.log('Ошибка при передаче или сохранении файлов.\nСервер отвечает (код 200). Но нет подтверждения от multiupload.php, что файлы сохранены')
                // alert('Ошибка (см. консоль)')
            }
        } catch (e) {
            // console.log(e)

            if(e.code === 'ERR_NETWORK') {
                // Нет подключения к серверу
                this.pushNotify( notifys[this.lang].errNetwork, 'notifyOrange')
                console.log('Нет подключения к серверу')
                return
            }

            // Обработка ошибок
            switch(e.response.data.status) {
                default:
                    this.pushNotify( notifys[this.lang].defaultError, 'notifyOrange')
                    // console.log(e)
                    console.log('Error: ' + e)
                    break
            } // switch
        }// try
        
    },// submitFiles



    // Подготовка к удалению картинки. Удаляю из стора и добавляю в массив picsToDelete - файлы, которые будут удалены (с сервера и базы, при сохранении FormUpdate)
    preparePicToDelete( filename ) {
        if( !filename && typeof filename !== 'string' ) {
            console.log('Ошибка: filename не передан или не string [см. preparePicToDelete()]');
            return }
        // // Включение заморозки действий
        // this.$store.commit('SetFreeze', true)
        // Формируем массив без элемента filename
        const filteredArr = this.$store.state.updProd.pictures.filter(item => item !== filename)
        // "Запоминаем" файл (в массиве), чтобы удалить позже при сохранении FormUpdate
        this.picsToDelete.push(filename)
        // Сохраняем обновленный массив в сторе
        this.$store.commit('UpdatePicsInDB', filteredArr)
    },// preparePicToDelete

    // Удаление файлов массива picsToDelete
    delPicsToDelete() {
        if(this.picsToDelete.length > 0) this.deleteFiles( this.picsToDelete )
        // Очищаем массив
        this.picsToDelete = []
    },

    // Удаление изображеняий, которые еще не сохранены в базе
    // filesToBeDel - массив или строка (имена файлов)
    // Если параметр не передан - удаляются все изображения (newPics), не сохраненные в базе
    // deleteNewPics( filesToBeDel ) {
    async deleteNewPics( filesToBeDel ) {
        // Если массив newPics пуст - прерываем функцию (поскольку нечего удалять)
        if(!this.newPics.length) return
        console.log('1) filesToBeDel: ', filesToBeDel)
        console.log('typeof: ', typeof filesToBeDel)

        // console.log('Удалить ' + filesToBeDel + '\n(с сервера и из массива newPics)');
        if( typeof filesToBeDel === 'string' ) filesToBeDel = [filesToBeDel]
        // console.log('Удалить все загруженные файлы.\n(с сервера и из массива newPics)');
        else filesToBeDel = this.newPics

        console.log('2) filesToBeDel: ', filesToBeDel);
        console.log('Удалить из newPics то что вернет deleteFiles() ');

        let delResult = await this.deleteFiles( filesToBeDel )
        // console.log('delResult: ', delResult);
        // Если функция deleteFiles не вернула массив прекращаем выполнение.
        if(!delResult) { console.log('deletedFiles - false'); return }
        // ---------------------
        // Если в newPics есть такой элемент, то удалем
        for( let i = 0; i < delResult.length; i++) {
            // фильтруем массив
            this.newPics = this.newPics.filter(item => item !== delResult[i])
        }// for

        // Если newPics опустел - скрываем прогрессбар, очищаем input и т.п.
        if(this.newPics.length === 0) this.hideBarAndClean()
        // ---------------------

    },// deleteNewPics

    // Удаляет файлы (изображений) с диска сервера
    // filesToBeDel - массив, обязательный параметр (список файлов, которые нужно удалить)
    // Возвращает массив (имена файлов, удаление которых подтверждено сервером)
    async deleteFiles( filesToBeDel ) {

        if( !filesToBeDel || typeof filesToBeDel === 'string' ) {
            console.log('Ошибка: функции deleteFiles не передан массив (обязательный параметр)')
            return }

        try {
            // let context = this
            const response = await axios({
                method: 'post',
                url: define.pathToAPI + '/multiupload/delete_uplfiles.php',
                data: {
                    // id: 'product-id',
                    filesToBeDel: filesToBeDel
                },
                // headers: {
                //     "Content-type": "application/json; charset=UTF-8"
                // }
                withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true")
                headers: {
                    // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                }
            })// axios
            console.log('response: ', response);
// #################################################
// Заменить алерты на пушнотифай и в целом подправить, что можно (обработку ответа, ошибок)
            // проверка ответа от php файла
            if(response.data) {

                if(response.data.doneFiles.length === 0) {
                    console.log('~~~');
                    console.log('Нет подтверждения от сервера');
                    console.log('Комментарии: ', response.data.comments);
                    console.log('response: ', response);
                    console.log('~~~');
                    alert('Нет подтверждения от сервера (см. консоль)')
                    return
                }// if
                
                // Возвращаем список удаленных файлов
                return response.data.doneFiles

            } else {
                alert('Ошибка (см. консоль)')
                console.log('Ошибка при передаче или сохранении файлов.\nСервер отвечает (код 200). Но нет подтверждения от multiupload.php, что файлы сохранены')
                return
            }// if
        } catch (e) {
            console.log(e)
            return
        }// try
// #################################################

    },// deleteFiles

    // Возврат элементов из picsToDelete в стор pictures
    getBackFiles() {
        if(this.picsToDelete.length > 0) this.$store.commit('AddToPicsInDB', this.picsToDelete)
        // Очищаем массив
        this.picsToDelete = []
    },// getBackFiles

    // Очистка массива файлов, скрытие прогресс-бара и очистка поля input
    hideBarAndClean() {
        // Очистка массива файлов
        if(this.newPics.length > 0) this.newPics = []

        const delay = 350 // ждем пока отработают transition у .fade  и .list (при transition .7s красиво с delay = 300 (350). То есть ~ половина transition)
        const afterSmooth = 600 // Если у .smooth transition: .5s (500 мсек), то afterSmooth ставим немного больше. Например, 600 (мсек)
        // Совет: для стилей .fade  и .list, нужно ставить одинаковую длительность transition. Чтобы переходы у списка и текста "Загружены файлы" отрабатывали синхронно.

        setTimeout(() => { this.isInProgress = false }, delay);
        this.$refs.files.value = ''
        setTimeout(() => { this.uploadPercentage = 0 }, delay + afterSmooth);
    },

},// methods
// watch: {
//     // При снятии заморозки, удаляем несохраненные данные
//     '$store.state.freeze': async function() {
//     }
// },// watch

// Composition API
setup() {
    const { lang } = useLang()
    const { pushNotify } = useNotify()

    return { define, maxNameLength, lang, pushNotify }
    // return { define, maxNameLength }
}


}
</script>

<style>

.picsArrBg {
    max-width: 540px;
    border: 0px solid #dbdbdb;
    border-bottom: 1px dashed #b1b1b1;
   
    border-radius: 8px 8px 0 0;
    background: #dfdfdf;
    /* padding-left: 15px; */
}

.graybg {
    max-width: 540px;
    border: 1px solid #cecece;
    border-radius: 8px;
    background: #f3f3f3;
    margin-bottom: 30px;
}

.multiSelect {
    padding-top: 15px;
    padding-left: 15px;
}

.multiSelect input {
    display: block;
    /* font-size: 1rem; */
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-clip: padding-box;
    border: 1px solid #cecece;

    border-radius: 8px 0 0 8px;
}

