/* .multiSelect input::file-selector-button {  */
    /* border-width: 2px 0; */
    border-color: #00a2ff #00000000 #0077ff;
    background: linear-gradient(#00a2ff, #0077ff) #0077ff;
    color: white;
    padding: 10px 12px;
    border-radius: 8px 0 0 8px;
    /* cursor: pointer; */

}

input[type=file]::file-selector-button:hover {
    border-color: #00a2ff #00000000 #0077ff;
    background: linear-gradient(#00a2ff, #008cff) #0077ff;
