.multiSelect button:disabled:hover {
    border: 1px solid #bababa;
    background: #e6e6e6;
    color: #a6a6a6;
    cursor: not-allowed;
}
.multiSelect button {
    border-color: #00a2ff #00000000 #0077ff;
    background: linear-gradient(#00a2ff, #0077ff) #0077ff;
    color: white;
    padding: 10px 12px;
    border-radius: 0 8px 8px 0;
    cursor: pointer;
}

.multiSelect button:hover {
    border-color: #00a2ff #00000000 #0077ff;
    background: linear-gradient(#00a2ff, #008cff) #0077ff;
}



.flex {
    display: flex;
    justify-content: space-between;
}

.flex input{
    flex-grow: 1;
}

.progress {
    display: flex;
    height: 15px;
    overflow: hidden;
    background-color: #d8d8d8;
    border-radius: 4px;
    width: 300px;
}

.progress-bar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    overflow: hidden;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-color: #28a745;
    transition: width .6s ease;
}

.fade-enter-active, .fade-leave-active { transition: opacity .7s ease; }

.fade-enter-from, .fade-leave-to { opacity: 0; }

.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active,
.list-leave-active {
  transition: all .7s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(20px);
}

