    list-style-type: none;
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    
    justify-content: flex-start;
} */
.flex-list {
    list-style-type: none;
    display: flex;
    align-items: center;
    margin-bottom: 7px;
    /* margin-bottom: 4px; */
}

