}

.picName a:hover {
    color: #008afc;
    text-decoration: underline;
}

.delPic {
    /* margin: 0px 6px; */
    /* margin-right: 4px; */
    padding: 2px;
    border: 2px solid transparent;
    border-radius: 20px;
    transition: all .2s ease;
}

.delPic:hover {
    cursor: pointer;
    border: 2px solid transparent;
    /* border: 2px solid #ed5050; */
    transform: scale(1.1);
    background: #ffffff;
    /* background: #ffecec; */
}

.delPic img {
    display: block;
}




.smooth {
    overflow: hidden;
    transition: all .5s ease-in-out;
    max-height: 0;
    opacity: 0;
}

.smooth-show {
    max-height: 100vh;
    opacity: 1;
}

.orange {
    font-weight: 700;
    color: orangered;
}

